.layout-row{
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    justify-content: space-between;

}
.layout-column {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: space-between;
}
.layout-wrapper{
    position: relative;
    z-index: -1;
}
.single-row{
    width: 100%;
    height: 2px;
    background-color: black;
}
.single-column{
    height: 100%;
    width: 2px;
    background-color: black;

}